h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.h1, h1 {}

.h2, h2 {
  @include bold;
  color: $black;
  line-height: 1.2;
  @include css-lock(24, 30, 576, 1199, font-size); }

.h3, h3 {}

.h4, h4 {
  color: $black;
  @include bold;
  @include tr(color .3s);
  @include css-lock(18, 21, 576, 1199, font-size);
  &--white {
    color: $white; } }

.h5, h5 {}

.h6, h6 {}

.text {
  color: $c-text;
  text-align: justify;
  font-size: var(--fz);

  &--center {
    text-align: center; }
  &--white {
    color: $white; } }

.text-block {
  background-color: $white;
  p {
    margin-bottom: 35px;
    @include md {
      margin-bottom: 25px; } } }
